.main__slider {
  position: relative;
  grid-column: 1/2;
  grid-row: 1/3;
  max-width: 540px;
  max-height: 490px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  img {
    width: 100%;
  }
}
.campaignTeaser__slider {
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  position: absolute;
  text-align: center;
  height: 100px;
  width: 289px;
  border-radius: 4px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-transform: uppercase;
  font-size: 22px;
  font-style: normal;
  font-weight: 600;
  line-height: 25px;
  letter-spacing: 0.06em;
}
.main__slider_text {
  font-size: 12px;
  font-weight: 600;
  line-height: 13px;
  letter-spacing: 0.06em;
  opacity: 0.4;
  margin: 12px;
}

.main__slider {
  .swiper-button-prev::after, .swiper-button-next::after {
    font-size: 16px;
  }
  .swiper-button-prev,.swiper-button-next {
    position: absolute;
    top: 50%;
    border-radius: 50%;
    background: #fff;
    width: 40px;
    height: 40px;
    font-size: 15px;
    z-index: 2;
    color: #121212;
    opacity: 0.6;
  }
}

@media (max-width: 1100px) {
  .main__slider {
    grid-column: 1 / 2;
    grid-row: 1/5;
    max-width: initial;
    .swiper-button-prev,.swiper-button-next {
      top: 160px;
    }
  }
  .campaignTeaser__slider {
    width: 25vw;
    top: 160px;
  }
}

@media (max-width: 790px) {
  .main__slider {
    grid-column: 1 / 3;
    grid-row: 1/4;
    .swiper-button-prev, .swiper-button-next {
      top: 200px;
    }
  }
  .campaignTeaser__slider {
    top: 45%;
    width: 40vw;
  }
}
@media (max-width: 660px) {
  .main__slider {
    grid-row: 1/3;
    .swiper-button-prev, .swiper-button-next {
      top: 130px;
    }
  }
  .campaignTeaser__slider {
    display: none;
  }
}